<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-select
              label="Thing Class"
              item-value="name"
              :disabled="true"
              :items="thingTypes"
              item-text="displayName"
              v-model="formValues.writer_thing_class"
            ></v-select>
          </v-col>

          <v-col cols="12" v-if="formValues.writer_thing_class === 'point'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Point"
              item-value="uuid"
              item-text="displayName"
              :items="allPoints"
              :loading="isFetchingAllDevices"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.writer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" v-if="formValues.writer_thing_class === 'schedule'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Scheulde"
              item-value="uuid"
              item-text="name"
              :items="schedules"
              :loading="isFetchingSchedules"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.writer_thing_uuid"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)"> {{ saveTitle }} </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import { THING_TYPES, THING_ACTIONS } from '../Producers/producers.constant';
export const initialFormValues = () => ({
  writer_thing_class: null,
  writer_thing_type: null,
  writer_thing_uuid: null,
  consumer_uuid: null,
});
export default {
  name: 'rf-writer-form',
  props: [
    'isEdit',
    'onCancel',
    'onSave',
    'item',
    'isSaving',
    'consumerId',
    'producerThingClass',
    'allPoints',
    'schedules',
  ],
  data: () => ({
    isFetchingAllDevices: false,
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
    valid: false,
    formValues: {
      ...initialFormValues(),
    },
    thingTypes: THING_TYPES,
    thingActions: THING_ACTIONS,
  }),
  computed: {
    lowerCaseTitle() {
      return 'writer';
    },
    titleCase() {
      return 'Writer';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.formValues,
        ...this.item,
      };
    }

    if (!this.isEdit && this.consumerId) {
      this.formValues = {
        ...this.formValues,
        writer_thing_class: this.producerThingClass,
        consumer_uuid: this.consumerId,
      };
    }
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error(`${this.lowerCaseTitle} form onSave function not passed`);
      }
      return this.onSave(this.formValues)
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.lowerCaseTitle}.`);
          this.cleanUp(true);
        })
        .catch(() => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.lowerCaseTitle}.`, {
            type: 'error',
          });
        });
    },
  },
};
</script>
