import BaseService from './baseService';
import endpoints from '../constants/endpoints';
import http from '../axios';
import _omit from 'lodash/omit';
import qs from 'query-string';

class RFWritersService extends BaseService {
  constructor() {
    super(endpoints.rfWriters);
  }

  fetchAll() {
    let endpoint = endpoints.rfWriters();
    return http.get(endpoint);
  }

  fetch = (query = {}) => {
    const consumerId = query.uuid || query.id;
    if (!consumerId) {
      throw new Error('Consumer Id is required for fetching writers of consumers');
    }

    let endpoint = endpoints.rfConsumers(consumerId);
    query = _omit(query, ['uuid', 'id']);

    if (query && Object.keys(query).length > 0) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }

    return http.get(endpoint).then(data => {
      return { dataItems: data.writers, extraData: _omit(data, ['writers']) };
    });
  };

  createWriter = body => {
    let url = this.evaluateUri();
    const endpoint = url + '/wizard';
    return http.post(endpoint, body);
  };
}

export const rfWritersService = new RFWritersService();
