
export const getDisplayName = (network, device, point) => {
  return `${network.name}:${device.name}:${point.name}`
}

export const getFlatPoints = (networks = []) => {
  let points = [];
  for (const network of networks) {
    if(!network.devices) {
      continue;
    }
    for (const device of network.devices) {
      if(!device.points) {
        continue;
      }
      for (const point of device.points) {
        const pObj = {
          ...point,
          networkName: network.name,
          deviceName: device.name,
          displayName: getDisplayName(network, device, point)
        };
        points.push(pObj);
      }
    }
  }
  return points;
}
