<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Writers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <writers-grid
            v-if="params.uuid"
            :params="params"
            :getDataCb="getDataCb"
            :allPoints="allPoints"
            :schedules="schedules"
            :producerThingClass="producerThingClass"
          >
          </writers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import WritersGrid from './writersGrid.vue';
import ROUTES from '../../../constants/routes';
import * as uiService from '../Common/uiService';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfNetworkService } from '../../../services/rfNetworkService';
import { rfScheduleService } from '../../../services/rfScheduleService';
import * as rfNetworkUiService from '../../../services/rfNetworkUiService';

export default {
  name: 'writers',
  data() {
    return {
      params: {
        uuid: null,
        with_writers: true,
      },
      renderCount: 0,
      producerThingClass: null,
      gridData: {},
      breadcrumbItems: [],
      schedules: [],
      allPoints: [],
    };
  },
  components: {
    IconButton,
    WritersGrid,
    Breadcrumbs,
    TooltipIconButton,
  },
  mounted() {
    this.initialize();
    this.fetchSchedules();
    this.fetchAllPoint();
  },
  watch: {
    '$route.params.consumerId': function (val, oldVal) {
      if (val != oldVal) {
        this.renderCount++;
        this.initialize();
      }
    },
    'flowNetworkCloneNamesMap': function () {
      this.setBreadCrumbs();
    },
  },
  computed: {
    flowNetworkCloneNamesMap() {
      return this.$store.state.flowNetworkCloneNamesMap;
    },
  },
  methods: {
    initialize() {
      this.params = { ...this.params, uuid: this.$route.params.consumerId };
      this.setBreadCrumbs();
    },
    fetchSchedules() {
      rfScheduleService.fetch().then(schedules => {
        this.schedules = schedules;
        return schedules;
      });
    },
    fetchAllPoint() {
      rfNetworkService.fetch({ with_points: true }).then(networks => {
        this.allPoints = rfNetworkUiService.getFlatPoints(networks);
        return networks;
      });
    },
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.producerThingClass = _get(this.gridData, 'extraData.producer_thing_class', null);
    },
    setBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push({
        text: 'All Flow Network Clone',
        align: 'center',
        disabled: false,
        link: ROUTES.rfFlowNetworkClone,
      });
      this.breadcrumbItems.push({
        text: uiService.getNameById(this.flowNetworkCloneNamesMap, this.$route.params.flowNetworkId),
        align: 'center',
        disabled: false,
        link: ROUTES.rfFlowNetworkStreamsClone.replace(':flowNetworkId', this.$route.params.flowNetworkId),
      });
      this.breadcrumbItems.push({
        text: uiService.getNameById(this.flowNetworkCloneNamesMap, this.$route.params.streamId),
        align: 'center',
        disabled: false,
        link: ROUTES.rfFlowNetworkCloneConsumers
          .replace(':flowNetworkId', this.$route.params.flowNetworkId)
          .replace(':streamId', this.$route.params.streamId),
      });
      this.breadcrumbItems.push({
        text: uiService.getNameById(this.flowNetworkCloneNamesMap, this.$route.params.consumerId),
        align: 'center',
        disabled: false,
      });
    },
  },
};
</script>
