<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
      :showExpand="true"
      :getDataCb="getDataCb"
      :singleExpand="true"
    >
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <writers-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :allPoints="allPoints"
          :schedules="schedules"
          :consumerId="consumerId"
          :producerThingClass="producerThingClass"
        ></writers-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <writers-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :schedules="schedules"
          :allPoints="allPoints"
          :consumerId="consumerId"
          :producerThingClass="producerThingClass"
        ></writers-form>
      </template>
      <template v-slot:afterActions="{ item, refreshGrid }">
        <tooltip-icon-button
          btnTooltip="Assign Point Action"
          btnIcon="mdi-yelp"
          btnColor="orange"
          v-if="item.item.writer_thing_class === 'point'"
          :onClick="() => toggleDialog(dialogNames.writerActionDialog, item.item, refreshGrid)"
        ></tooltip-icon-button>
      </template>
      <template v-slot:expansionIcon="{ expand, isExpanded, item }">
        <tooltip-icon-button
          btnTooltip="Assign Schedule Action"
          btnIcon="mdi-calendar"
          btnColor="orange"
          buttonSize="medium"
          v-if="item.writer_thing_class === 'schedule'"
          :onClick="() => onExpandClick({ expand, isExpanded, item })"
        ></tooltip-icon-button>
      </template>
      <template v-slot:expansionView="{ headers, item }">
        <div>
          <v-progress-circular
            size="20"
            indeterminate
            v-if="!selectedCalendarData && isLoadingCalendar"
          ></v-progress-circular>
          <calendar-view
            v-if="selectedCalendarData"
            :key="renderCalendarView"
            :data="selectedCalendarData"
            :config="selectedCalendarConfig"
            :isLoadingCalendar="isLoadingCalendar"
            :updateData="(data, config) => updateCalendarView(item.uuid, data, config)"
          ></calendar-view>
        </div>
      </template>
    </generic-grid>

    <v-dialog v-model="dialog[dialogNames.writerActionDialog]" persistent max-width="500px">
      <point-writer-actions-form
        v-if="dialog[dialogNames.writerActionDialog]"
        :onCancel="() => closeDialog(dialogNames.writerActionDialog)"
        :item="selectedItem"
        :onSave="onAssignWriteAction"
      ></point-writer-actions-form>
    </v-dialog>
  </div>
</template>
<script>
import CalendarView from '../RfEvents/CalendarView.vue';
import WritersForm from './writersForm.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfWritersService } from '../../../services/rfWritersService';
import PointWriterActionsForm from '../WriterActions/pointWriterActionsForm.vue';
import * as rfWriterActionsService from '../../../services/rfWriterActionsService';

const dialogNames = {
  writerActionDialog: 'writerActionDialog',
  scheduleActionDialog: 'scheduleActionDialog',
};

export default {
  name: 'rf-writers-grid',
  components: { GenericGrid, WritersForm, TooltipIconButton, PointWriterActionsForm, CalendarView },
  props: ['params', 'getDataCb', 'allPoints', 'schedules', 'producerThingClass'],
  data() {
    return {
      renderCalendarView: 0,
      selectedItem: {},
      selectedCalendarConfig: {},
      selectedCalendarData: null,
      isLoadingCalendar: false,
      calendarData: {},
      syncGrid: f => f,
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.writerActionDialog]: false,
        [dialogNames.scheduleActionDialog]: false,
      },
      gridName: gridNames.RF_WRITER,
      service: rfWritersService,
      toolbarTitle: 'Writers',
      gridHeaders: [
        {
          text: 'Thing class',
          align: 'center',
          value: 'writer_thing_class',
          sortable: true,
        },
        {
          text: 'Thing type',
          align: 'center',
          value: 'writer_thing_type',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    consumerId() {
      return this.$route.params.consumerId;
    },
    consumer() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
  methods: {
    toggleDialog(dialogName, item, syncGrid) {
      this.selectedItem = item;
      this.syncGrid = syncGrid;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    onAssignWriteAction(body) {
      const uuid = this.selectedItem.uuid;
      return rfWriterActionsService.postAction(uuid, body).then(data => {
        if (typeof this.syncGrid === 'function') this.syncGrid();
        return data;
      });
    },
    onExpandClick({ expand, isExpanded, item }) {
      const value = !isExpanded;
      expand(value);
      if (value) {
        this.selectedItem = item;
        this.fetchCalendarData();
      } else {
        this.selectedCalendarData = null;
      }
    },
    updateCalendarView(writerUUID, calendarModel, calendarConfig) {
      return rfWriterActionsService.patchCalendarSchedule(writerUUID, calendarModel, calendarConfig).then(data => {
        this.fetchCalendarData();
        return data;
      });
    },
    fetchCalendarData() {
      const selectedUUID = this.selectedItem.uuid;
      this.isLoadingCalendar = true;
      return rfWritersService
        .fetchById(selectedUUID)
        .then(data => {
          const schedules = (data && data.data_store && data.data_store.schedules) || {};
          const config = (data && data.data_store && data.data_store.config) || {};
          this.calendarData[selectedUUID] = {
            ...schedules,
          };
          this.selectedCalendarConfig = { ...config };
          this.selectedCalendarData = { ...schedules };
          this.renderCalendarView++;
          return data;
        })
        .finally(() => {
          this.isLoadingCalendar = false;
        });
    },
  },
};
</script>
