<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="6">
            <v-select
              v-if="!priorityArrayOnly"
              label="Action"
              v-model="formValues.action"
              :items="writerActions"
            ></v-select>
          </v-col>
          <v-col cols="1"></v-col>
          <v-col cols="5" class="">
            <v-switch
              v-model="formValues.ask_refresh"
              inset
              class="pr-4"
              label="Ask Refresh"
              color="primary"
              v-if="!priorityArrayOnly"
            ></v-switch>
          </v-col>

          <v-row dense>
            <v-col cols="12" class="text-left">
              <span class="caption">Priority Array</span>
            </v-col>
            <v-col cols="3" v-for="priorityKey in priorityList" :key="priorityKey">
              <v-text-field
                single-line
                dense
                outlined
                required
                step="0.01"
                type="number"
                v-if="formValues.priority"
                v-model="formValues.priority[priorityKey]"
                :label="priorityKey"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Close </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)">
        Send <v-icon>mdi-send</v-icon></v-btn
      >
    </v-card-actions>
  </v-card>
</template>
<script>
import _cloneDeep from 'lodash/cloneDeep';
const WRITER_ACTIONS = {
  WRITE: 'write',
  READ: 'read',
};

export const initialFormValues = ({ action, ask_refresh, priority = {} } = {}, priorityArrayOnly = false) => {
  let formValues = {
    action: action || WRITER_ACTIONS.WRITE,
    ask_refresh: typeof ask_refresh === 'undefined' ? true : ask_refresh,
    priority: {
      _1: priority['_1'] || null,
      _2: priority['_2'] || null,
      _3: priority['_3'] || null,
      _4: priority['_4'] || null,
      _5: priority['_5'] || null,
      _6: priority['_6'] || null,
      _7: priority['_7'] || null,
      _8: priority['_8'] || null,
      _9: priority['_9'] || null,
      _10: priority['_10'] || null,
      _11: priority['_11'] || null,
      _12: priority['_12'] || null,
      _13: priority['_13'] || null,
      _14: priority['_14'] || null,
      _15: priority['_15'] || null,
      _16: priority['_16'] || null,
    },
  };
  if (priorityArrayOnly) {
    delete formValues.action;
    delete formValues.ask_refresh;
  }
  return formValues;
};
export default {
  name: 'rf-point-writer-actions-form',
  props: ['onCancel', 'onSave', 'item', 'priorityArrayOnly'],
  data: vm => ({
    isSaving: false,
    writerActions: Object.values(WRITER_ACTIONS),
    priorityList: [
      '_1',
      '_2',
      '_3',
      '_4',
      '_5',
      '_6',
      '_7',
      '_8',
      '_9',
      '_10',
      '_11',
      '_12',
      '_13',
      '_14',
      '_15',
      '_16',
    ],
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
    valid: false,
    formValues: {
      ...initialFormValues({}, vm.priorityArrayOnly),
    },
  }),
  computed: {
    lowerCaseTitle() {
      return 'write priority array';
    },
    titleCase() {
      return 'Write Priority Array';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.item && Object.keys(this.item).length > 0) {
      this.formValues = initialFormValues(
        {
          ask_refresh: this.item.ask_refresh,
          action: this.item.action,
          priority: this.item.data_store || this.item.priority,
        },
        this.priorityArrayOnly,
      );
    }
  },
  methods: {
    cleanUp() {
      this.formValues = initialFormValues({}, this.priorityArrayOnly);
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    transformFormValues(values) {
      let formValues = _cloneDeep(values);
      formValues.priority = Object.keys(formValues.priority).reduce((acc, curr) => {
        if (formValues.priority[curr]) {
          acc[curr] = +formValues.priority[curr];
        } else {
          acc[curr] = null;
        }
        return acc;
      }, {});
      return formValues;
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error(`${this.lowerCaseTitle} form onSave function not passed`);
      }
      this.isSaving = true;
      return this.onSave(this.transformFormValues(this.formValues))
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.lowerCaseTitle}.`);
        })
        .catch(err => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.lowerCaseTitle}.`, {
            type: 'error',
          });
        })
        .finally(() => {
          this.isSaving = false;
        });
    },
  },
};
</script>
