import http from '../axios';
import endpoints from '../constants/endpoints';

const rfWriterActionsEndpoint = endpoints.rfWriterActions;

export const postAction = (uuid, body) => {
  const endpoint = rfWriterActionsEndpoint(uuid);
  return http.post(endpoint, body);
};

export const patchCalendarSchedule = (uuid, calendarSchedules, calendarConfig = {}) => {
  const endpoint = rfWriterActionsEndpoint(uuid);
  const payload = {
    ask_refresh: true,
    action: 'write',
    schedule: {
      schedules: calendarSchedules,
      config: calendarConfig,
    },
  };

  return http.post(endpoint, payload);
};
